import React, {memo, useEffect, useState} from "react";
import Amap from "./Amap";
import SideBar from "../Partials/SideBar";
import Header from "../../partials/Header";
import ReactDOM from "react-dom";
import {NavLink} from "react-router-dom";
import $ from "jquery";
import M from "materialize-css";
// library estefadeh shode baray traslation
import {Get_All_clicnic, Delete_Clinic, Get_one_Clinic} from "../../../../entity/Redux/Admin/Action";
import {useTranslation} from "react-i18next";
import {useDispatch, useSelector} from "react-redux";
import Loading from "../../partials/Loading";

export default memo(() => {
    const [clinicData, setClinicData] = useState({
        clinicName: "Defual name",
        clinicAddress: "Defual address - avn - streen 3",
        clinicPostal: "+9897945",
        clinicPhoneNumber: "961754544812",
        clinicFilePath: [],
        clinicLocation: [109, 34],
    });

    const ClinicData = useSelector(state => state.Admin.ClinicAll);
    const ClinicOne = useSelector(state => state.Admin.ClinicOne);

    console.log("9*999999999999 : ", ClinicOne ? ClinicOne : '');
    const dispatch = useDispatch();

    useEffect(() => {
        $("#back_shado").fadeOut();

        dispatch(Get_All_clicnic());

        var mateilizeBox = document.querySelectorAll(".materialboxed");
        M.Materialbox.init(mateilizeBox);
    }, []);

    const {t, i18n} = useTranslation();

    {
        /*
        -- language
        -- do zabaneh kardan site
        */
    }

    function handleLang(e) {
        // e.preventDefault();
        // sabt type language dar storage
        if (!localStorage.getItem("lan")) {
            localStorage.setItem("lan", "en");
        }
        //daryaft type language as localstorage
        var lan = localStorage.getItem("lan");
        var ckeckbox = $("input[name=cahngelan]");
        if (ckeckbox.is(":checked") && lan === "en") {
            localStorage.setItem("lan", "ch");
        } else {
            localStorage.setItem("lan", "en");
        }
        let lang = localStorage.getItem("lan");
        // gharar dadan language dar library translate
        i18n.changeLanguage(lang);
    }

    // end language

    const HandleDeleteClinic = (e, id) => {
        e.preventDefault();
        dispatch(Delete_Clinic(id));
    }

    const HandleMoreDetail = (e, id) => {
        e.preventDefault();
        dispatch(Get_one_Clinic(id));
    }

    return (
        <div>
            <div id="loader-wrapper">
                <div id="loader"></div>
                <div className="loader-section section-left"></div>
                <div className="loader-section section-right"></div>
            </div>

            <Header/>
            <div id="main">
                <div className="wrapper">
                    <SideBar/>
                    <div id="back_shado"></div>
                    <div className="container">
                        <div className="row">
                            <div className="col s12" style={{marginBottom: 30}}>
                                <div className="col s12">
                                    <div id="breadcrumbs-wrapper">
                                        <div className="container">
                                            <div className="row">
                                                <div className="col s10 m6 l6">
                                                    <h5 className="breadcrumbs-title">Clinic Address</h5>
                                                    <ol className="breadcrumbs">
                                                        <li>
                                                            <NavLink to="/admin">
                                                                <a>Dashboard</a>
                                                            </NavLink>
                                                        </li>

                                                        <li>
                                                            <a className="active">Clinic Address</a>
                                                        </li>
                                                    </ol>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>

                                <div className="col s12 l6">
                                    <div className="container" id="map-back">
                                        <ul
                                            id="projects-collection"
                                            className="collection z-depth-1"
                                        >
                                            <li className="collection-item avatar clinic">
                                                <i className="material-icons" id="custom_icon_clinic">my_location</i>
                                                <h6 className="collection-header m-0">Clinic Address</h6>
                                                <p>List of all Clinic address</p>


                                            </li>

                                            <ul className="clinic-list">
                                                <li>
                                                    <ul className="first">
                                                        <li>Clinic Name</li>
                                                        <li>PhoneNumber</li>
                                                        <li>Action</li>
                                                    </ul>
                                                </li>


                                                {ClinicData ? ClinicData.data.data.map(item => (
                                                    <li>
                                                        <ul>
                                                            <li>{item.name}</li>
                                                            <li>{item.phoneNumber}</li>
                                                            <li id="actions">
                                                                <a id="action">
                                                                    <i className="material-icons"
                                                                       onClick={e => HandleDeleteClinic(e, item.id)}>delete</i>
                                                                </a>

                                                                <a id="action">
                                                                    <span onClick={e => HandleMoreDetail(e, item.id)}>more detail</span>
                                                                </a>
                                                            </li>
                                                        </ul>
                                                    </li>
                                                )) : <Loading/>}

                                            </ul>
                                        </ul>
                                    </div>
                                </div>

                                <div className="col s12 l6">
                                    {ClinicOne ? (
                                        <div className={"container"} id="map-back">
                                            <div id="map">
                                                <Amap datas={ClinicOne.data.data.location} show={"show"}/>
                                            </div>
                                            <div className="col s12">
                                                <div
                                                    className="input-field col s12 l6"
                                                    style={{marginTop: 0}}
                                                >
                                                    <p style={{color: "#ccc"}}>Clinic Name</p>
                                                    <div id="input_back">
                                                        <span className={"disabledd"}></span>
                                                        <input
                                                            placeholder="Clinic Name"
                                                            autoComplete="false"
                                                            name="name"
                                                            required
                                                            value={ClinicOne.data.data.name}
                                                            type="text"
                                                            className="validate"
                                                        />
                                                    </div>
                                                    {/*<label htmlFor="password">Password</label>*/}
                                                </div>

                                                <div
                                                    className="input-field col s12 l6"
                                                    style={{marginTop: 0}}
                                                >
                                                    <p style={{color: "#ccc"}}>Clinic addresss</p>
                                                    <div id="input_back">
                                                        <span className={"disabledd"}></span>
                                                        <input
                                                            value={ClinicOne.data.data.address}
                                                            placeholder="Clinic Address"
                                                            autoComplete="false"
                                                            name="address"
                                                            required
                                                            type="text"
                                                            className="validate"
                                                        />
                                                    </div>
                                                    {/*<label htmlFor="password">Password</label>*/}
                                                </div>

                                                <div
                                                    className="input-field col s12 l6"
                                                    style={{marginTop: 0}}
                                                >
                                                    <p style={{color: "#ccc"}}>Clinic Phone Number</p>
                                                    <span className={"disabledd"}></span>
                                                    <div id="input_back">
                                                        <span className={"disabledd"}></span>
                                                        <input
                                                            placeholder="Clinic Phone Number"
                                                            autoComplete="false"
                                                            name="phoneNumber"
                                                            required
                                                            value={ClinicOne.data.data.phoneNumber}
                                                            type="text"
                                                            className="validate"
                                                        />
                                                    </div>
                                                    {/*<label htmlFor="password">Password</label>*/}
                                                </div>

                                                <div className="col s12" style={{marginBottom: 20}}>
                                                    <p style={{color: "#ccc"}}>Clinic Image</p>


                                                    {ClinicOne.data.data.attachment ? ClinicOne.data.data.attachment.map(item => (
                                                        <div
                                                            className="doc_doctor"
                                                            style={{
                                                                position: "relative",
                                                                overflow: "hidden",
                                                                height: "auto",
                                                            }}
                                                        >
                                                            <img
                                                                className="materialboxed"
                                                                src={item}
                                                                style={{
                                                                    width: "100%",
                                                                    height: "100%",
                                                                    borderRadius: "5px",
                                                                }}
                                                            />
                                                        </div>

                                                    )) : (
                                                        <p>nothing image uploaded</p>
                                                    )}


                                                    {/* ) : ( */}
                                                    {/* <a href={"#"}>
                              <i className="material-icons">picture_as_pdf</i>
                            </a> */}
                                                    {/* )} */}

                                                </div>

                                            </div>
                                        </div>
                                    ) : <p style={{color: '#fff'}}>select clinic for show detail :)</p>}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
});
