import React, { useEffect, useState } from "react";
import $ from "jquery";
import { Map, MouseTool , Marker } from "react-amap";

const layerStyle = {
  padding: "10px",
  background: "#fff",
  border: "1px solid #ddd",
  borderRadius: "4px",
  position: "absolute",
  top: "10px",
  left: "10px",
};

const Amap = (props) => {
  const [position , setPosition] = useState({});

  // setPosition(props.datas)


  const mapPlugins = ["ToolBar"];
  const mapCenter = props.datas;

  return (
      <div>
        <div style={{ width: "100%", height: 370 }}>
          <Map
              amapkey={"styles/afdd93fc70e330480ae6a54d3d8d4e79"}
              plugins={mapPlugins}
              center={mapCenter}
              setMapStyle={"styles/afdd93fc70e330480ae6a54d3d8d4e79"}
          >
            <Marker position={props.datas} />
          </Map>
        </div>

      </div>
  );
};

export default Amap;
